.chatWindow {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.chatWindow--header {
    height: 60px;
    border-bottom: 1px solid #CCC;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.chatWindow--headerinfo {
    display: flex;
    align-items: center;
    cursor: pointer;
}
.chatWindow--avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-left: 15px;
    margin-right: 15px;
}
.chatWindow--name {
    font-size: 17px;
    color: black;
}
.chatWindow--headerbuttons {
    display: flex;
    align-items: center;
    margin-right: 15px;
}
.chatWindow--btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: hidden;
    transition: all ease 0.3s;
}
.chatWindow--body {
    flex: 1;
    overflow-y: auto;
    background-color: #E5DDD5;
    background-size: cover;
    background-position: center;
    background-image: url('https://i.pinimg.com/236x/ec/2b/9d/ec2b9df200760591db7fe82418b48be7.jpg');
}
.chatWindow--footer {
    height: 62px;
    display: flex;
    align-items: center;

}
.chatWindow--pre {
    display: flex;
    margin: 0 15px;
}
.chatWindow--inputarea {
    flex: 1;
}
.chatWindow--input {
    width: 100%;
    height: 40px;
    border: 0;
    outline: 0;
    background-color: #FFF;
    border-radius: 15px;
    font-size: 15px;
    color: #4A4A4A;
    padding-left: 15px;
}
.chatWindow--pos {
    display: flex;
    margin: 0 15px;
}

.chatWindow--emojiarea {
    height: 200px;
    overflow-y: hidden;
    transition: all ease 0.3s;
}

aside.emoji-picker-react {
    width: auto;
    background: none;
}
.emoji-picker-react .emoji-group:before {
    background: none;
}