.chatListItem {
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 70px;
}
.chatListItem:hover {
    background-color: #F5F5F5;
}
.chatListItem.active {
    background-color: #EBEBEB;
}

.chatListItem--avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 15px;
}
.chatListItem--lines {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #EEE;
    padding-right: 15px;
    margin-left: 15px;
    flex-wrap: wrap;
    min-width: 0;
}
.chatListItem--line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.chatListItem--name {
    font-size: 17px;
    color: black;
}
.chatListItem--date {
    font-size: 12px;
    color: #999;
}
.chatListItem--lastMsg {
    font-size: 14px;
    color: #999;
    display: flex;
    width: 100%;
}
.chatListItem--lastMsg p {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
}
.chatlist::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
.chatlist::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.205);
}

.contentarea {
    flex: 1;
}